@use "@styles/pseudo-elements" as *;

.coin-fav {
    cursor: pointer;
    position: relative;
    margin: {
        left: 12px;
    }
    &::after {
        @include star-icon(16px);
        top: -4px;
    }
    &_active {
        &::after {
            @include star-yellow-icon(16px);
            top: -4px;
        }
    }
}