@mixin arrow-square($size, $width: 2px, $color: black, $angle: 45deg) {
    @include pseudo-element();
    display: block;
    @include square($size);
    border-style: solid;
    border {
        color: $color;
    }    
    border: {
        top: 0;
        right: 0;
    }
    @include place-icon-in-center($size);
    transform: rotate( $angle );
}

@mixin icon($name) {
    background: {
        image: url("@assets/#{$name}");
        size: cover;
    }
}

@mixin square-icon($size, $name) {
    @include pseudo-element();
    display: inline-flex;
    @include icon($name);
    @include square($size);
    @include place-icon-in-center($size);
}

@mixin pseudo-element() {
    content: "";
    position: absolute;
}

@mixin square($size) {
    width: $size;
    height: $size;
}

@mixin place-icon-in-center($size) {
    left: 0px;
    position: absolute;
    margin-top: calc($size / 2 * -1);
    top: 50%;
}

@mixin star-yellow-icon($size) {
    @include square-icon($size, "staryellow.svg");
}

@mixin star-icon($size) {
    @include square-icon($size, "star.svg");
}

@mixin search-icon($size) {
    @include square-icon($size, "iconsearch.svg");
}

@mixin settings-icon($size) {
    @include square-icon($size, "gear.svg");
}

@mixin arrowback-icon($size) {
    @include square-icon($size, "arrowback.svg");
}
