.text {
  font: {
    family: sans-serif;
  }

  &_size_st {
    font-size: 10px;
  }
  &_size_es {
    font-size: 12px;
  }
  &_size_s {
    font-size: 14px;
  }
  &_size_ms {
    font-size: 16px;
  }
  &_size_m {
    font-size: 18px;
  }
  &_size_ml {
    font-size: 20px;
  }
  &_size_l {
    font-size: 24px;
  }

  &_color {
    &_accent {
      color: #212529;
    }
    &_descend {
      color: #6C757D;
    }
    &_red {
      color: #D90429;
    }
    &_green {
      color: #21BF73;
    }
    &_blue {
      color: #0063F5;
    }
    &_dark {
      color: #343A40;
    }
  }

  &_weight {
    &_t {
      font-weight: 450;
    }
    &_m {
      font-weight: 500;
    }
    &_b {
      font-weight: 700;
    }
  }
}