@use "@styles/_sizes" as *;

.market-overview {
  width: var(--page_width);
  background: {
    color: var(--app_bg-color);
  }
  height: var(--market-overview__height);

  &__top {
    display: flex;
    justify-content: space-between;
    margin: {
      top: var(--market-overview__top_margin_top);
      bottom: var(--market-overview__top_margin_bottom);
      right: var(--market-overview__top_margin_right);
      left: var(  --market-overview__middle-top_margin_left)
    }
  }

  &__middle {
    &-top {
      display: flex;
      justify-content: space-between;
      margin: {
        bottom: var(--market-overview__middle-top_margin_bottom);
        left: var(  --market-overview__middle-top_margin_left);
        right: var(  --market-overview__middle-top_margin_right);
      }
    }
  }

  &__select-currency {
    width: 98px;
  }

  &__bottom {
    margin: {
      top: var(--coins__list_margin-top);
    }
  }

  &__search {
    margin: {
      right: var(--market-overview__search_margin_right);
    }
  }
}