.controls-scale__item {
  position: relative;
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    padding: {
      top: 6px;
      right: 10px;
      bottom: 6px;
      left: 10px;
    }
    border-radius: 20px;
    background-color: #F8F9FA;
    left: -10px;
    top: -6px;
    z-index: -1;
    border: 0.5px solid #DFE2E4;
  }
  &_active {
    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      padding: {
        top: 6px;
        right: 10px;
        bottom: 6px;
        left: 10px;
      }
      border-radius: 20px;
      background-color: #ECF4FF;
      left: -10px;
      top: -6px;
      z-index: -1;
      border: 0.5px solid #0063F5;
    }
  }
}